import React, {useState} from 'react'
import './Home.css'
import Fade from 'react-reveal/Fade'
import {Bounce} from 'react-reveal'
import {Link} from 'react-scroll'
import Particles from 'react-particles-js'
import Typewriter from 'typewriter-effect'
import ArrowDropDownCircleIcon from '@material-ui/icons/ArrowDropDownCircle'
import Navbar from '../navbar/Navbar'
import config from '../../config'
import profile from '../../images/profile.png'
import linkedin from '../../images/social/linkedin.png'
import mail from '../../images/social/mail.png'

const Home = () => {
    const [imageLoaded, setImageLoaded] = useState(false)
    return (
        <div className="home-wrapper">
            {/*<div style={{position: 'fixed', right: '0%',zIndex:1001}}>
                <div style={{padding: 10, display:'flex'}}>
                    <Bounce cascade>
                        <div style={{display: 'flex', justifyContent: 'center'}}>
                            <a style={{padding: 4}}
                               href="https://www.linkedin.com/in/dchavezarmenteros/"
                               target="_blank"
                               rel="noopener noreferrer"
                            >
                                <img src={linkedin} alt="Linkedin Logo" width="50px"/>
                            </a>
                            <a style={{padding: 4}}
                               href={'mailto:${davidarmenteros296@gmail.com}'}
                               target="_blank"
                               rel="noopener noreferrer"
                            >
                                <img src={mail} alt="Mail Logo" width="50px"/>
                            </a>
                        </div>

                    </Bounce>
                   
                </div>
            </div>*/}
            <div className="home">
                <Particles className="particles" params={config.particles}/>

                <div className={`greeting${!imageLoaded ? ' hide' : ''}`}>
                    <Fade bottom distance="40px">
                        <img
                            className="profile"
                            alt="m.jigalin profile"
                            src={profile}
                            onLoad={() => setImageLoaded(true)}
                        />
                        <h1 className="greeting-text">
                            Hi, I'm <span className="name">David ChÃ¡vez Armenteros</span>.{' '}
                            <span className="wave-emoji" role="img" aria-label="waving hand">
                ðŸ‘‹
              </span>
                        </h1>
                        <h1 className="greeting-text">
                            <Typewriter
                                options={{
                                    strings: [
                                        'I love video games.',
                                        'I love programming.',
                                        'I love learning new tech.',
                                    ],
                                    autoStart: true,
                                    loop: true,
                                    deleteSpeed: 10,
                                    cursor: '<',
                                    delay: 100,
                                }}
                            />
                        </h1>
                        <Bounce cascade>
                            <a href={'https://drive.google.com/file/d/1eSgOirvje3vzFDi4xeyXzHkv8E6sNAaE/view?usp=share_link'} target={'_blank'} className='resume-btn'>Download Resume</a>
                        </Bounce>
                        <div className="scroll-down">
                            <Link
                                activeClass="active"
                                to="projects"
                                spy={true}
                                smooth={true}
                                offset={-63}
                                duration={500}
                            >
                                <div>
                                    <strong><p className={'pulse'} style={{cursor: 'pointer'}}>View Projects</p>
                                    </strong>
                                    <ArrowDropDownCircleIcon
                                        fontSize="large"
                                        style={{pointerEvents: 'fill', cursor: 'pointer'}}
                                    />
                                </div>
                            </Link>
                        </div>
                    </Fade>
                </div>
                <Navbar/>
            </div>
        </div>
    )
}

export default Home
