import React from 'react'
import './Contact.css'
import Section from '../section/Section'
import {Bounce} from 'react-reveal'
import ContactForm from '../contactForm/ContactForm'
import linkedin from '../../images/social/linkedin.png'
import github from '../../images/social/github.png'
import facebook from '../../images/social/facebook.png'
import mail from '../../images/social/mail.png'

const Contact = () => {
    return (
        <Section title="Contact">
            
            {/*<ContactForm />*/}
            <div style={{display: 'flex', justifyContent: 'center'}}>
                <p>You can get in touch through my LinkedIn or Mail</p>
            </div>
            <div style={{display: 'flex', justifyContent: 'center'}}>
                <a className="textLink" href={'mailto:${davidarmenteros296@gmail.com}'}
                   target="_blank" rel="noopener noreferrer">
                    davidarmenteros296@gmail.com
                </a>
            </div>
            <Bounce cascade>
                <div className="links">
                    <a
                        href="https://www.linkedin.com/in/dchavezarmenteros/"
                        target="_blank"
                        rel="noopener noreferrer"
                    >
                        <img src={linkedin} alt="Linkedin Logo" width="40px"/>
                    </a>

                    <a style={{padding: 4}}
                       href={'mailto:${davidarmenteros296@gmail.com}'}
                       target="_blank"
                       rel="noopener noreferrer"
                    >
                        <img src={mail} alt="Mail Logo" width="50px"/>
                    </a>
                </div>
            </Bounce>
        </Section>
    )
}

export default Contact
